<script>
import { Icon } from "@iconify/vue2";

export default {
  name: 'UserActivity',
  components: {
    Icon
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 25,
      filters: {
        userId: '',
        action: '',
        startDate: '',
        endDate: ''
      },
      stats: {
        activeToday: 12,
        totalLogins: 156,
        avgSession: '24m',
      },
      users: [
        { id: 1, name: 'Ahmad Pratama', email: 'ahmad@example.com' },
        { id: 2, name: 'Siti Nurhaliza', email: 'siti@example.com' },
        { id: 3, name: 'Budi Santoso', email: 'budi@example.com' },
        { id: 4, name: 'Dewi Sartika', email: 'dewi@example.com' },
        { id: 5, name: 'Rudi Habibie', email: 'rudi@example.com' }
      ],
      activities: [
        {
          id: 1,
          userId: 1,
          userName: 'Ahmad Pratama',
          userEmail: 'ahmad@example.com',
          action: 'login',
          details: 'User berhasil login ke sistem',
          timestamp: new Date('2024-01-15T09:30:00'),
          ipAddress: '192.168.1.100',
          device: 'Desktop',
          status: 'Success'
        },
        {
          id: 2,
          userId: 2,
          userName: 'Siti Nurhaliza',
          userEmail: 'siti@example.com',
          action: 'create',
          details: 'Membuat laporan baru: "Laporan Bulanan Q1"',
          timestamp: new Date('2024-01-15T10:15:00'),
          ipAddress: '192.168.1.101',
          device: 'Mobile',
          status: 'Success'
        },
        {
          id: 3,
          userId: 3,
          userName: 'Budi Santoso',
          userEmail: 'budi@example.com',
          action: 'update',
          details: 'Memperbarui profil pengguna',
          timestamp: new Date('2024-01-15T11:00:00'),
          ipAddress: '192.168.1.102',
          device: 'Tablet',
          status: 'Success'
        },
        {
          id: 4,
          userId: 1,
          userName: 'Ahmad Pratama',
          userEmail: 'ahmad@example.com',
          action: 'view',
          details: 'Melihat dashboard analytics',
          timestamp: new Date('2024-01-15T11:30:00'),
          ipAddress: '192.168.1.100',
          device: 'Desktop',
          status: 'Success'
        },
        {
          id: 5,
          userId: 4,
          userName: 'Dewi Sartika',
          userEmail: 'dewi@example.com',
          action: 'login',
          details: 'Gagal login - password salah',
          timestamp: new Date('2024-01-15T12:00:00'),
          ipAddress: '192.168.1.103',
          device: 'Mobile',
          status: 'Failed'
        },
        {
          id: 6,
          userId: 2,
          userName: 'Siti Nurhaliza',
          userEmail: 'siti@example.com',
          action: 'delete',
          details: 'Menghapus laporan draft',
          timestamp: new Date('2024-01-15T13:15:00'),
          ipAddress: '192.168.1.101',
          device: 'Desktop',
          status: 'Success'
        },
        {
          id: 7,
          userId: 5,
          userName: 'Rudi Habibie',
          userEmail: 'rudi@example.com',
          action: 'logout',
          details: 'User logout dari sistem',
          timestamp: new Date('2024-01-15T14:00:00'),
          ipAddress: '192.168.1.104',
          device: 'Desktop',
          status: 'Success'
        }
      ]
    }
  },
  computed: {
    filteredActivities() {
      let filtered = [...this.activities];
      
      if (this.filters.userId) {
        filtered = filtered.filter(activity => activity.userId == this.filters.userId);
      }
      
      if (this.filters.action) {
        filtered = filtered.filter(activity => activity.action === this.filters.action);
      }
      
      if (this.filters.startDate) {
        const startDate = new Date(this.filters.startDate);
        filtered = filtered.filter(activity => activity.timestamp >= startDate);
      }
      
      if (this.filters.endDate) {
        const endDate = new Date(this.filters.endDate + 'T23:59:59');
        filtered = filtered.filter(activity => activity.timestamp <= endDate);
      }
      
      return filtered.sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
    },
    
    totalPages() {
      return Math.ceil(this.filteredActivities.length / this.pageSize);
    },
    
    startIndex() {
      return (this.currentPage - 1) * this.pageSize;
    },
    
    endIndex() {
      return this.startIndex + this.pageSize;
    },
    
    paginatedActivities() {
      return this.filteredActivities.slice(this.startIndex, this.endIndex);
    },
    
    visiblePages() {
      const pages = [];
      const start = Math.max(1, this.currentPage - 2);
      const end = Math.min(this.totalPages, this.currentPage + 2);
      
      for (let i = start; i <= end; i++) {
        pages.push(i);
      }
      
      return pages;
    }
  },
  methods: {
    formatDate(date) {
      return new Intl.DateTimeFormat('id-ID', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      }).format(new Date(date));
    },
    
    formatTime(date) {
      return new Intl.DateTimeFormat('id-ID', {
        hour: '2-digit',
        minute: '2-digit'
      }).format(new Date(date));
    },
    
    getActionIcon(action) {
      const icons = {
        'login': 'mdi:login',
        'logout': 'mdi:logout',
        'create': 'mdi:plus-circle',
        'update': 'mdi:pencil-circle',
        'delete': 'mdi:delete-circle',
        'view': 'mdi:eye-circle'
      };
      return icons[action] || 'mdi:circle';
    },
    
    getActionColor(action) {
      const colors = {
        'login': 'text-green-500',
        'logout': 'text-gray-500',
        'create': 'text-blue-500',
        'update': 'text-yellow-500',
        'delete': 'text-red-500',
        'view': 'text-purple-500'
      };
      return colors[action] || 'text-gray-500';
    },
    
    getActionLabel(action) {
      const labels = {
        'login': 'Login',
        'logout': 'Logout',
        'create': 'Create',
        'update': 'Update',
        'delete': 'Delete',
        'view': 'View'
      };
      return labels[action] || action;
    },
    
    getDeviceIcon(device) {
      const icons = {
        'Desktop': 'mdi:desktop-classic',
        'Mobile': 'mdi:cellphone',
        'Tablet': 'mdi:tablet'
      };
      return icons[device] || 'mdi:help-circle';
    },
    
    getStatusBadgeClass(status) {
      return status === 'Success' 
        ? 'bg-green-100 text-green-800' 
        : 'bg-red-100 text-red-800';
    },
    
    applyFilters() {
      this.currentPage = 1;
      this.$message.info('Filter berhasil diterapkan');
    },
    
    previousPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    },
    
    goToPage(page) {
      this.currentPage = page;
    },
    
    refreshActivity() {
      this.$message.success('Data aktivitas berhasil diperbarui');
      // Simulate refresh
    },
    
    exportActivity() {
      this.$message.info('Export aktivitas sedang diproses...');
      // Implement export functionality
    }
  },
  
  watch: {
    pageSize() {
      this.currentPage = 1;
    }
  },
  
  mounted() {
    // Set default date filter to last 7 days
    const today = new Date();
    const lastWeek = new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);
    
    this.filters.endDate = today.toISOString().split('T')[0];
    this.filters.startDate = lastWeek.toISOString().split('T')[0];
  }
}
</script>

